import { Component, Inject } from '@angular/core';
import { NavController, NavParams, Platform, ViewController, ActionSheetController, App, AlertController, normalizeURL } from 'ionic-angular';
import { Camera, CameraOptions } from '@ionic-native/camera';
import { AuthService } from '../../../providers/auth-service';
import { ImagesProvider } from '../../../providers/images/images';
import { FormControl, FormBuilder, FormGroup } from "@angular/forms";
import { AuthHttp, AuthModule, JwtHelper, tokenNotExpired } from 'angular2-jwt';


/**
 * Generated class for the CommunityWritePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */


@Component({
  selector: 'page-community-write',
  templateUrl: 'community-write.html',
})
export class CommunityWritePage {

  images: any = [];
  photoSrc: any;
  cameraPhoto: any;
  imagePath: any;
  imagePath2: any;
  item: FormControl;
  note = { select: '', title: '', contents: '', tags: [], id: '' };
  userData: any;
  jwtHelper: JwtHelper = new JwtHelper();
  mode: any;

  topics = [];
  name: string;
  talks = [];
  preparedTags = [];
  skinQna: boolean = false;
  // ,preparedTags = [
  //   '#Ionic',
  //   '#Angular',
  //   '#Javascript',
  //   '#Mobile',
  //   '#Hybrid',
  //   '#CrossPlatform'
  // ]



  topic() {
    console.log(this.topics);
  }

  addTalk() {
    this.talks.push({ name: this.name, topics: this.topics });
  }


  ionViewWillLoad() {
  }

  constructor(private imagesProvider: ImagesProvider, public _camera: Camera, public actionSheetCtrl: ActionSheetController, public nav: NavController, public navParams: NavParams, public platform: Platform, private auth: AuthService, public viewCtrl: ViewController, private alertCtrl: AlertController, public app: App) {

    this.platform.ready().then((readySource) => {

      if (this.navParams.get('qna')) {
        this.skinQna = true;
      }

      this.platform.registerBackButtonAction(() => {
        let nav = app._appRoot._getActivePortal() || app.getActiveNav();
        let activeView = nav.getActive();

        if (activeView != null) {
          if (this.nav.canGoBack()) { // CHECK IF THE USER IS IN THE ROOT PAGE.
            //this.nav.pop(); // IF IT'S NOT THE ROOT, POP A PAGE.
          }
          else if (activeView.isOverlay) {
            //activeView.dismiss();
          }
          else {
            // backgroundMode.moveToBackground();
            let alert = this.alertCtrl.create({
              cssClass: 'push_alert_cancel',
              title: "plinic",
              message: "  ?",
              buttons: [
                {
                  text: '',
                  role: 'cancel',
                  handler: () => {
                    console.log('');
                  }
                },
                {
                  text: '',
                  handler: () => {
                    console.log(''),
                      this.viewCtrl.dismiss();
                  }
                }]
            });
            alert.present();
          }
        }
      });
    });
  }


  ionViewDidLoad() {
    console.log('ionViewDidLoad CommunityWritePage');
  }

  ionViewCanEnter() {
    this.loadItems();
    this.getHashTags();
  }

  attache_image_hide() {
    if (this.imagePath2) {
      document.getElementById("attache_image").style.display = "none";
    }
  }

  attache_image_view() {
    if (this.imagePath2)
      document.getElementById("attache_image").style.display = "";
  }

  public dissmiss() {
    let alert = this.alertCtrl.create({
      cssClass: 'push_alert_cancel',
      title: "plinic",
      message: "  ?",
      buttons: [
        {
          text: '',
          role: 'cancel',
          handler: () => {
            console.log('');
          }
        },
        {
          text: '',
          handler: () => {
            console.log(''),
              this.viewCtrl.dismiss();
          }
        }]
    });
    alert.present();
  }

  public camera() {
    let actionSheet = this.actionSheetCtrl.create({
      title: ' ',
      buttons: [
        {
          text: ' ',
          handler: () => {
            this.takePicture(this._camera.PictureSourceType.PHOTOLIBRARY);
          }
        },
        {
          text: ' ',
          handler: () => {
            this.takePicture(this._camera.PictureSourceType.CAMERA);
          }
        },
        {
          text: '',
          role: 'cancel'
        }
      ]
    });
    actionSheet.present();
  }

  public takePicture(sourceType) {
    // Create options for the Camera Dialog
    var options = {
      quality: 50,
      destinationType: this._camera.DestinationType.FILE_URI,
      sourceType: sourceType,
      saveToPhotoAlbum: true,
      encodingType: this._camera.EncodingType.JPEG,
      mediaType: this._camera.MediaType.PICTURE,
      allowEdit: true,
      correctOrientation: true,
      targetWidth: 300,
      targetHeight: 300
    };

    // Get the data of an image
    this._camera.getPicture(options).then((imagePath) => {
      if (this.platform.is('ios')) {
        this.imagePath = imagePath;
        this.imagePath = normalizeURL(this.imagePath);
        this.imagePath2 = normalizeURL(this.imagePath);
      }
      else {
        // if (imagePath == null) {
        //       alert("  .");
        //       return false;
        //   }
        //   //    ?123234234      ,
        //   //   ?   .
        //   var p = imagePath.toLowerCase().lastIndexOf('?');
        //   if (p > -1) {
        //       imagePath = imagePath.substring(0, p);
        //   }
        //   //     ,     .
        //   if (imagePath.toLowerCase().lastIndexOf('.') < 0) {
        //       imagePath += '.jpg';
        //   }
        this.imagePath = imagePath;
        // this.imagePath = "data:image/jpeg;base64," + this.imagePath;
        this.imagePath = this.imagePath;
        this.imagePath2 = this.imagePath;
        this.auth.setUserStorageimagePath(this.imagePath2);
      }
    }, (err) => {
      console.log('Error: ', err);
    });
  }

  public loadItems() {
    this.auth.getUserStorage().then(items => {

      if (items.from === 'kakao' || items.from === 'google' || items.from === 'naver') {
        this.userData = {
          accessToken: items.accessToken,
          id: items.id,
          age_range: items.age_range,
          birthday: items.birthday,
          email: items.email,
          gender: items.gender,
          nickname: items.nickname,
          profile_image: items.profile_image,
          thumbnail_image: items.thumbnail_image,
        };
      } else {
        this.userData = {
          accessToken: items.accessToken,
          id: items.id,
          age_range: items.age_range,
          birthday: items.birthday,
          email: this.jwtHelper.decodeToken(items).email,
          gender: items.gender,
          nickname: this.jwtHelper.decodeToken(items).name,
          profile_image: items.profile_image,
          thumbnail_image: items.thumbnail_image,
        };
        // console.log(this.userData);
      }
    });
  }

  registerNote() {
    let alert = this.alertCtrl.create({
      cssClass: 'push_alert_cancel',
      title: " ",
      message: "   ?",
      buttons: [
        {
          text: '',
          role: 'cancel',
          handler: () => {
          }
        },
        {
          text: '',
          handler: () => {
            if (this.mode === true) {
              // this.note.id = this.id;
              // console.log("update Id :" + this.id);
              this.auth.noteSave(this.userData.email, this.note, this.imagePath2).then(data => {
                if (!data) {
                  let alert2 = this.alertCtrl.create({
                    cssClass: 'push_alert',
                    title: ' ',
                    message: "   .",
                    buttons: [
                      {
                        text: '',
                        handler: () => {
                          this.nav.pop();
                        }
                      }
                    ]
                  });
                  alert2.present();
                }
                // this.nav.push(CareZoneMissionIngPage, { _id: id });
              }, error => {
                this.showError(JSON.parse(error._body).msg);
              });

            } else {
              if (this.skinQna) {
                if (this.imagePath2) { //  
                  this.auth.communitySkinQnaImgSave(this.userData.email, this.note, this.imagePath2).then(data => {
                    if (data) {
                      let alert2 = this.alertCtrl.create({
                        cssClass: 'push_alert',
                        title: ' ',
                        message: "    .",
                        buttons: [
                          {
                            text: '',
                            handler: () => {
                              this.nav.pop();
                            }
                          }
                        ]
                      });
                      alert2.present();
                    }
                    // this.nav.push(CareZoneMissionIngPage, { _id: id });
                  }, error => {
                    this.showError(JSON.parse(error._body).msg);
                  });
                } else { //  
                  this.auth.communitySkinQnaSave(this.userData.email, this.note).subscribe(data => {
                    if (data !== "") {
                      let alert2 = this.alertCtrl.create({
                        cssClass: 'push_alert',
                        title: ' ',
                        message: "    .",
                        buttons: [
                          {
                            text: '',
                            handler: () => {
                              this.nav.pop();
                            }
                          }
                        ]
                      });
                      alert2.present();
                    }
                    // this.nav.push(CareZoneMissionIngPage, { _id: id });
                  }, error => {
                    this.showError(JSON.parse(error._body).msg);
                  });

                }
              } else {
                //  
                if (this.imagePath2) {
                  this.auth.noteSave(this.userData.email, this.note, this.imagePath2).then(data => {
                    if (data) {
                      let alert2 = this.alertCtrl.create({
                        cssClass: 'push_alert',
                        title: ' ',
                        message: "    .",
                        buttons: [
                          {
                            text: '',
                            handler: () => {
                              this.nav.pop();
                            }
                          }
                        ]
                      });
                      alert2.present();
                    }
                  }, error => {
                    this.showError(JSON.parse(error._body).msg);
                  });
                } else { //    
                  this.auth.noteNoImgSave(this.userData.email, this.note).subscribe(data => {
                    if (data !== "") {
                      let alert2 = this.alertCtrl.create({
                        cssClass: 'push_alert',
                        title: ' ',
                        message: "    .",
                        buttons: [
                          {
                            text: '',
                            handler: () => {
                              this.nav.pop();
                            }
                          }
                        ]
                      });
                      alert2.present();
                    }
                  }, error => {
                    this.showError(JSON.parse(error._body).msg);
                  });
                }
              }
            }
          }
        }]
    });
    alert.present();


  }

  getHashTags() {
    this.auth.getHashTags().subscribe(items => {
      if (items !== '' || !undefined || !null) {
        // console.log(items[0].tags);
        this.preparedTags = items[0].tags.split(",");
        console.log(this.preparedTags);
      }
    });
  }


  showError(text) {
    //this.loading.dismiss();

    let alert = this.alertCtrl.create({
      cssClass: 'push_alert',
      title: 'Plinic',
      message: text,
      buttons: [{
        text: ''
      }]
    });
    alert.present();

  }


}



